@import 'design.scss';

.footer {
  height: 160px !important;
  padding: 40px 0px;
  background-color: $secondary-color;
  color: $secondary-font-color;
  font-size: 0.8em;
  font-style: italic;
  text-align: center;
  .footer-link-icon {
    width: 40px;
    height: 40px;
    margin: 0px 20px;
    filter: invert(100%);
  }
  
}

.footer > .col > .row > * {
  margin: 5px auto;
}

