$separation-color: lightgray;

// General Container
.all-blog-container {
  min-height: 80vh;
  color: #36373B;
  padding-top: 40px;
  padding-bottom: 40px;

  // Left (Post Viewing) Panel
  .whats-new-container {
    font-weight: bold;
    font-size: 30px;
  }
  .post-container, .whats-new-container {
    padding: 0px 30px;
  }
}